<template>
  <div class="example">
    <v-contextmenu ref="contextmenu" :theme="theme" :disabled="disabled">
      <v-contextmenu-item @click="handleClick">1</v-contextmenu-item>
      <v-contextmenu-item disabled @click="handleClick">2</v-contextmenu-item>

      <v-contextmenu-item divider></v-contextmenu-item>

      <v-contextmenu-submenu title="">
        <v-contextmenu-item @click="handleClick">3</v-contextmenu-item>

        <v-contextmenu-item divider></v-contextmenu-item>

        <v-contextmenu-submenu disabled title="">
          <v-contextmenu-item @click="handleClick">5</v-contextmenu-item>
        </v-contextmenu-submenu>

        <v-contextmenu-item @click="handleClick">4</v-contextmenu-item>
      </v-contextmenu-submenu>
    </v-contextmenu>

    <div :class="['box', theme]" v-contextmenu:contextmenu @click="handleSwitchDisabled">
      
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Disabled',

    props: {
      theme: String,
    },

    data () {
      return {
        disabled: false,
      }
    },

    methods: {
      handleClick (vm, event) {
        console.log(vm, event)
      },
      handleSwitchDisabled () {
        this.disabled = !this.disabled
      },
    },
  }
</script>

<style scoped>
  .box {
    width: 100%;
  }
</style>
